<template>
  <div class="api-layout">
    <doc-content></doc-content>

    <div class="emulated-phone">
      <m-title type="1">Button</m-title>

      <m-panel title="">
        <m-button-group type="column">
          <m-button></m-button>
          <m-button type="primary"></m-button>
          <m-button type="minor"></m-button>
          <m-button type="warn"></m-button>
          <m-button type="danger"></m-button>
        </m-button-group>
      </m-panel>

      <m-panel title="">
        <m-button-group>
          <m-button inline type="primary"></m-button>
          <m-button inline type="minor"></m-button>
        </m-button-group>
      </m-panel>

      <m-panel title="">
        <m-button-group>
          <m-button inline plain type="primary"></m-button>
          <m-button inline plain type="minor"></m-button>
        </m-button-group>
      </m-panel>

      <m-panel title="">
        <m-button-group>
          <m-button inline round type="primary"></m-button>
          <m-button inline round type="minor"></m-button>
        </m-button-group>
      </m-panel>

      <m-panel title="">
        <m-button-group>
          <m-button inline disabled type="primary"></m-button>
          <m-button inline disabled type="minor"></m-button>
        </m-button-group>
      </m-panel>

      <m-panel title="">
        <m-button-group>
          <m-button inline size="large" type="primary"></m-button>
          <m-button inline size="normal" type="minor"></m-button>
          <m-button inline size="small" type="warn"></m-button>
          <m-button inline size="mini" type="danger"></m-button>
        </m-button-group>
      </m-panel>

      <m-panel title="">
        <m-button-group>
          <m-button inline text type="primary"></m-button>
          <m-button inline text type="minor"></m-button>
        </m-button-group>
      </m-panel>

      <m-panel title="">
        <m-button-group>
          <m-button inline loading type="primary"></m-button>
          <m-button inline loading plain type="minor"></m-button>
        </m-button-group>
      </m-panel>

      <m-panel title="">
        <m-button-group>
          <m-button inline round icon="android-done" type="primary"></m-button>
          <m-button inline round icon="android-search"></m-button>
          <m-button inline plain round icon="android-delete" type="minor"></m-button>
          <m-button inline plain round icon="android-star" type="warn"></m-button>
        </m-button-group>
      </m-panel>

