      </m-panel>

      <m-panel title="">
        <m-button-group>
          <m-button inline color="#8b21ff"></m-button>
          <m-button inline color="#ffdc33"></m-button>
          <m-button inline plain color="#a16711"></m-button>
        </m-button-group>
      </m-panel>

      <m-panel title="">
        <m-button-group>
          <m-button type="minor" inline link="/"></m-button>
