      <m-panel title="">
        <m-button-group type="row">
          <m-button type="primary"></m-button>
          <m-button type="danger" plain></m-button>
        </m-button-group>
      </m-panel>

      <m-panel title="">
        <m-button-group type="column">
          <m-button type="primary"></m-button>
          <m-button type="danger" plain></m-button>
        </m-button-group>
