<template>
  <div class="api-layout">
    <doc-content></doc-content>

    <div class="emulated-phone">
      <m-title type="1">Dialog</m-title>

      <m-panel title="">
        <m-box>
          <m-button @click="showDialog1">dialog</m-button>
        </m-box>
      </m-panel>

      <m-panel title="">
        <m-box>
          <m-button @click="showDialog2">dialog</m-button>
        </m-box>
      </m-panel>

      <m-panel title="">
        <m-box>
          <m-button @click="showDialog3">dialog</m-button>
        </m-box>
      </m-panel>

      <m-dialog v-model="dialogShow1" closeable>
        
      </m-dialog>

      <m-dialog v-model="dialogShow2" title="" ref="dialog2">
        <div></div>
        <m-button slot="foot" type="primary" text @click="$refs.dialog2.close()"></m-button>
      </m-dialog>

      <m-dialog v-model="dialogShow3" title="" title-align="left" icon="android-bulb" ref="dialog3">
        <div></div>
        <m-button-group type="right" slot="foot">
          <m-button inline size="small" @click="$refs.dialog3.close()"></m-button>
          <m-button inline size="small" type="primary" @click="$refs.dialog3.close()"></m-button>
        </m-button-group>
      </m-dialog>
    </div>
  </div>
</template>

<script>
import Content from '../doc/Dialog'

export default {
  name: 'exp-dialog',
  components: { DocContent: Content },
  data () {
    return {
      dialogShow1: false,
      dialogShow2: false,
      dialogShow3: false
    }
  },
  methods: {
    showDialog1 () {
      this.dialogShow1 = true
    },
    showDialog2 () {
      this.dialogShow2 = true
    },
    showDialog3 () {
      this.dialogShow3 = true
    }
  }
}
</script>
