<template>
  <div class="api-layout">
    <doc-content></doc-content>

    <div class="emulated-phone">
      <m-title type="1">Input</m-title>

      <m-panel title="Input">
        <m-box>
          <m-input v-model="text1" placeholder="" />
        </m-box>
      </m-panel>

      <m-panel title="">
        <m-box>
          <m-input v-model="text2" placeholder="" disabled />
        </m-box>
      </m-panel>

      <m-panel title="">
        <m-box>
          <m-input v-model="text3" placeholder="" readonly />
        </m-box>
      </m-panel>

      <m-panel title="">
        <m-box>
          <m-input v-model="text4" placeholder="" clearable @clear="clearHandler" />
        </m-box>
      </m-panel>

      <m-panel title="">
        <m-cell>
          <m-input v-model="text6" type="tel" placeholder="" maxlength="4" clearable>
            <m-button type="minor" inline size="small" @click="sendCode"></m-button>
          </m-input>
        </m-cell>
        <m-cell>
          <m-input v-model="text6" type="tel" placeholder="" maxlength="4" clearable>
            <span class="customFoot">cm</span>
          </m-input>
        </m-cell>
      </m-panel>

      <m-panel title="">
        <m-box>
          <m-input v-model="text7" placeholder="" error />
        </m-box>
      </m-panel>

      <m-panel title="">
        <m-box>
          <m-input v-model="text8" type="textarea" rows="5" maxlength="200" placeholder="" @change="handlerChange" />
        </m-box>
      </m-panel>
    </div>
  </div>
</template>

<script>
import Content from '../doc/Input'

export default {
  name: 'exp-input',
  components: { DocContent: Content },
  data () {
    return {
      text1: '',
      text2: '',
      text3: 'readonly input ',
      text4: '',
      text5: '',
      text6: '',
      text7: '13000000000',
      text8: ''
    }
  },
  methods: {
    clearHandler () {
      this.$toast('')
    },

    sendCode () {
      this.$toast('')
    },

    handlerChange (val) {
      this.$toast(val)
    }
  }
}
</script>

<style lang="scss" scoped>
.customFoot {
  margin-left: 5px;
  font-size: 12px;
  color: #bfbfbf;
}
</style>
