<template>
  <div class="api-layout">
    <doc-content></doc-content>

    <div class="emulated-phone">
      <m-title type="1">Toast</m-title>

      <m-panel title="">
        <m-box>
          <m-button @click="showToast1">toast</m-button>
        </m-box>
      </m-panel>

      <m-panel title="">
        <m-button-group>
          <m-button inline type="primary" @click="showToast2"></m-button>
          <m-button inline type="warn" @click="showToast3"></m-button>
          <m-button inline type="danger" @click="showToast1"></m-button>
        </m-button-group>
      </m-panel>

      <m-panel title="">
        <m-box>
          <m-button @click="showToast4">toast</m-button>
        </m-box>
      </m-panel>

      <m-panel title="">
        <div slot="head"></div>
        <m-box>
          <m-button @click="showToast5">toast</m-button>
        </m-box>
      </m-panel>

      <m-panel title="">
        <m-button-group>
          <m-button type="minor" inline  @click="showToast6"></m-button>
          <m-button type="warn" inline  @click="showToast7"></m-button>
          <m-button type="danger" inline  @click="showToast8"></m-button>
        </m-button-group>
      </m-panel>
    </div>
  </div>
</template>

<script>
import Content from '../doc/Toast'

export default {
  name: 'exp-toast',
  components: { DocContent: Content },
  methods: {
    showToast1 () {
      this.$toast('')
    },
    showToast2 () {
      this.$toast.top('')
    },
    showToast3 () {
      this.$toast.center('')
    },
    showToast4 () {
      this.$toast({
        message: '3',
        timeout: 3000,
        type: 'bottom'
      })
    },
    showToast5 () {
      this.$toast.center({
        icon: 'android-alarm-clock',
        message: '',
        timeout: 2000
      })
    },
    showToast6 () {
      this.$toast.success('')
    },
    showToast7 () {
      this.$toast.warning('')
    },
    showToast8 () {
      this.$toast.error('')
    }
  }
}
</script>
