<template>
  <div class="api-layout">
    <doc-content></doc-content>

    <div class="emulated-phone">
      <m-title type="1">Alert</m-title>

      <m-panel title="">
        <m-box>
          <m-button @click="showAlert1">alert</m-button>
        </m-box>
      </m-panel>

      <m-panel title="">
        <m-box>
          <m-button @click="showAlert2">alert</m-button>
        </m-box>
      </m-panel>

      <m-panel title="">
        <m-box>
          <m-button @click="showAlert3">alert</m-button>
        </m-box>
      </m-panel>
    </div>
  </div>
</template>

<script>
import Content from '../doc/Alert'

export default {
  name: 'exp-alert',
  components: { DocContent: Content },
  methods: {
    showAlert1 () {
      this.$alert('')
    },

    showAlert2 () {
      this.$alert({
        title: '',
        message: '',
        confirmTxt: ''
      })
    },

    showAlert3 () {
      this.$alert({
        title: '',
        message: '',
        confirmTxt: '',
        onConfirm () {
          this.$toast('')
        }
      })
    }
  }
}
</script>
