<template>
  <div class="api-layout">
    <doc-content></doc-content>

    <div class="emulated-phone">
      <m-title type="1">Loading</m-title>

      <m-panel title="">
        <m-box>
          <m-button @click="showLoading1">loading</m-button>
        </m-box>
      </m-panel>

      <m-panel title="">
        <m-box>
          <m-button @click="showLoading2">loading</m-button>
        </m-box>
      </m-panel>
    </div>
  </div>
</template>

<script>
import Content from '../doc/Loading'

export default {
  name: 'exp-loading',
  components: { DocContent: Content },
  methods: {
    showLoading1 () {
      this.$loading.show()
      setTimeout(() => {
        this.$loading.hide()
      }, 2000)
    },

    showLoading2 () {
      this.$loading.show('')
      setTimeout(() => {
        this.$loading.hide()
      }, 2000)
    }
  }
}
</script>
