<template>
  <div class="api-layout">
    <doc-content></doc-content>

    <div class="emulated-phone">
      <m-title type="1">Confirm</m-title>

      <m-panel title="">
        <m-box>
          <m-button @click="showConfirm1">confirm</m-button>
        </m-box>
      </m-panel>

      <m-panel title="">
        <m-box>
          <m-button @click="showConfirm2">confirm</m-button>
        </m-box>
      </m-panel>

      <m-panel title="">
        <m-box>
          <m-button @click="showConfirm3">confirm</m-button>
        </m-box>
      </m-panel>
    </div>
  </div>
</template>

<script>
import Content from '../doc/Confirm'

export default {
  name: 'exp-confirm',
  components: { DocContent: Content },
  data () {
    return {
      bbb: '1234'
    }
  },
  methods: {
    showConfirm1 () {
      this.$confirm('')
    },

    showConfirm2 () {
      this.$confirm({
        title: '',
        message: '',
        confirmTxt: '',
        cancelTxt: ''
      })
    },

    showConfirm3 () {
      this.$confirm({
        title: '',
        message: '',
        confirmTxt: '',
        cancelTxt: '',
        onConfirm () {
          this.$toast('')
        },
        onCancel () {
          this.$toast('')
        }
      })
    }
  }
}
</script>
